import React from 'react'
import { bool, func, number, string } from 'prop-types'
import classNames from 'classnames'

import styles from './MediaRange.module.scss'

const MediaRange = ({ inverse, max, min, name, onChange, step, value }) => (
  <input
    className={classNames(styles.MediaRange, inverse && styles.inverse)}
    max={max}
    min={min}
    name={name}
    onChange={onChange}
    step={step}
    style={{ backgroundSize: `${(value / max) * 100}% 100%` }}
    type="range"
    value={value}
  />
)

MediaRange.defaultProps = {
  max: 100,
  min: 0
}

MediaRange.propTypes = {
  inverse: bool,
  max: number,
  min: number,
  name: string.isRequired,
  onChange: func,
  step: number,
  value: string
}

export default MediaRange
